@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

body {
  font-family: 'Montserrat', sans-serif;
}

.app {
  padding-top: 5rem;
  display: flex;
  justify-content: center;

}

.wrap {
  width: 500px;
}

$height: 46px;

.select {

  &__input {
    border: 1px solid #ccc;
    border-radius: 10px;
    height: $height;
    display: flex;
    align-items: center;
    padding: 0 1rem;
  }

  &__dropdown {}
}